* {
  box-sizing: border-box;
}

/* global vars */
:root {
  --bg-dark-black: #242424;
  --bg-light-black: #333333;
  --highlight-blue: #57F2FF;
  --highlight-blue-hover: #00b7db;
  --text-light: #EBEBEB;
  --text-dark: #969696;
  --valid-green: #05FF2B;
  --invalid-red: #eb4034;
}

/* Navbar -------------------------------------------------------------------- */
nav {
  display: flex;
  align-items: center; /* centers elements vertically in navbar */
  justify-content: space-between;
  height: 70px;
  padding: 0px 20px; /* padding top and bottom = 0 */
  background-color: var(--bg-dark-black);
  border-bottom: 1px solid var(--bg-light-black);
}

nav a {
  color: var(--text-light);
  text-decoration: none;
}

/* styles the Link element inside navbar */
nav div a {
  margin-right: 20px;
}

strong {
  font-weight: bolder;
}

.title {
  display: none;
}

.logo {
  width: 50px;
}

