  transition: 0.3s ease-in-out all;
} */

.icon:hover {
  color: var(--highlight-blue-hover);
}

.faIcon {
  margin-left: 10px;
}

/* Signin/up form ----------------------------------------------------------------- */
section {
  max-width: 500px;
  margin: 0 auto; /* center align */
  margin-top: 20px;
  padding: 10px 10px;
}

section h1 {
  text-align: center;
  font-size: 2.2rem;
  color: var(--text-light);
  margin: 10px;
}

section .logo {
  width: 30%;
  display: block;
  margin: 0 auto;
}

section .min {
  width: 15%; /* smaller sign up logo */
}

section h2 {
  text-align: center;
  font-size: 1.3rem;
  color: var(--text-light);
  margin: 10px;
}

section .form {
  margin-top: 30px;
  padding: 0px 50px;
}

.input_container {
  margin-top: 20px;
}

.input_container input {
  width: 100%;
  font-size: 1rem;
  padding: 10px;
  outline: none;
  margin-top: 10px;
  border: 1px solid var(--bg-light-black);
  border-radius: 25px;
  box-shadow: inset 0 0 3px var(--bg-light-black);
}

.btn_container {
  margin: 20px 0px;
  text-align: center;
}

.btn {
  width: 100%;
  padding: 10px;
  border-radius: 25px;
  outline: none;
  background: var(--highlight-blue);
  border: none;
  color: var(--bg-dark-black);
  cursor: pointer;
  font-size: 1.5rem;
  box-shadow: 0 0 5px var(--bg-dark-black);
  margin-top: 10px;
  /* transition: 0.3s ease-in-out all; */
  transition: all 0.3s;
}

