  background: var(--bg-dark-black);
  color: var(--text-light);
  cursor: pointer;
  transition: 0.3s ease-in-out all;
  font-size: 16px;
} */

.btn:hover {
  /* transform: scale(1.05); */
  background-color: var(--highlight-blue-hover);
  color: var(--text-light);
}

.errorMsg {
  text-align: center;
  color: var(--invalid-red);
}

/* Profile ------------------------------------------------------------------- */
.profile_container {
  display: flex;
  align-items: center;
}

.img_container {
  position: relative; /* because overlay position is absolute */
  margin-right: 20px;
  margin: 0 auto;
}

.img_container img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 1px solid var(--bg-light-black);
  transition: 0.5s ease-in-out all;
}

.img_container:hover img {
  opacity: 0.2;
}

.img_container:hover .overlay {
  opacity: 1;
}

.overlay {
  transition: 0.2s ease;
  opacity: 0; /* not visible until hover */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.text_container {
  text-align: center;
}

/* Home ---------------------------------------------------------------- */
/* put all user section in radio box */
/* if checked - display grid */
/* if not checked - user section display none or grid ratio 0fr 1fr */
/* add transition animation */
/* desktop view remains as original grid */
.home_section {
  position: relative;
  display: grid;
  /* users section = 1 third, message section = 2 thirds */
  /* grid-template-columns: 1fr 3fr; */
  /* grid-template-columns: 2fr 3fr; */
  /* grid-template-columns: 1fr 5fr; */
  grid-template-columns: 1fr 2fr;
  overflow: hidden; /* hides scrollbar */
  height: calc(100vh - 70px); /* mitigate navbar height which is 70px */
  width: 100vw;
}

.users_section {
  /* display: none; */
  margin-top: 10px;
  border-right: 2px solid var(--highlight-blue);
  border-bottom: 1px solid var(--highlight-blue);
  overflow-y: auto; /* scrollbar visible only if user list becomes too long */
  height: calc(100vh - 149px);
}

.messages_container {
  position: relative;
  width: 100%;
}

.messages_user {
  /* padding: 10px; */
  text-align: center;
  border-bottom: 2px solid var(--highlight-blue);
}

.no_convo {
  font-size: 1rem;
  color: var(--text-light);
  width: 100%; /* to allow text align to work */
  text-align: center;
}

img[alt="waving hand"] {
 display: block;
 margin-left: auto;
 margin-right: auto;
 margin-top: 10px;
 width: 3rem;
}

.messages {
  height: calc(100vh - 200px);
  /* height: 100vh; */
  overflow-y: auto;
  border-bottom: 1px solid var(--highlight-blue);
}

.chat_container {
  width: 100vw;
  background: #05FF2B;
  position: fixed;
  bottom: 0;
  left: 0;
}

.none {
  display: none;
}

/* Users ------------------------------------------------------------------ */
.user_wrapper {
  margin-bottom: 10px;
  padding: 10px;
  cursor: pointer;
}

.user_info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.user_detail {
  display: flex;
  align-items: center;
}

.user_detail h4 {
  display: none;
  margin-left: 10px;
}

.avatar {
  width: 60px;
  /* height: 50px; */
  border-radius: 50%;
  margin-left: 40%;
  /* border: 1px solid var(--bg-light-black); */
}

/* online/offline indicator */
.user_status {
  width: 53px;
  height: 53px;
  border-radius: 50%;
}

.online {
  border: 3px solid var(--valid-green);
}

.offline {
  border: 3px solid var(--invalid-red);
}

.selected_user {
  background: var(--bg-light-black);
}

.truncated {
  font-size: 14px;
  white-space: nowrap;
  width: 90%;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 0;
}

.truncated strong {
  margin-right: 10px;
}

.unread {
  margin-left: 10px;
  background: var(--highlight-blue);
  color: white;
  padding: 2px 4px;
  border-radius: 10px;
}

/* removes mobile friend avatars from large screens */
/* .sm_screen {
  display: none;
} */

/* Chat ---------------------------------------------------------------- */
.message_form {
  position: absolute;
  bottom: 0;
  left: 20%;
  width: 100%;
  height: 30px;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.message_form input {
  width: 40vw;
  margin: 0px 25px 10px;
  padding: 10px;
  border-radius: 5px;
  outline: none;
  border: none;
}

.message_form .btn {
  margin-top: -10px;
}

/* Message ------------------------------------------------------ */
/* .message_wrapper {
  margin-top: 5px;
  padding: 0px 5px;
} */

.message_wrapper img {
  width: 50%;
  border-radius: 5px;
}

.message_wrapper p {
  padding: 20px;
  /* display: inline-block; */
  width: 75%;
  text-align: center;
  border-radius: 25px;
  width: fit-content;
}

.message_wrapper small {
  display: block;
  margin-top: 5px;
  margin-right: 10%;
  opacity: 0.7;
  text-align: right;
}

.me {
  background: var(--highlight-blue);
  color: var(--bg-dark-black);
  margin-left: auto;
  margin-right: 5px;
}

.friend {
  background: var(--text-light);
  color: var(--bg-dark-black);
  margin-left: 5px;
}

/* Privacy Policy --------------------------------------------------- */
.btnPrivacy {
  padding: 10px;
  outline: none;
  border: none;
  background: none;
  color: var(--highlight-blue);
  cursor: pointer;
  transition: 0.3s ease-in-out all;
  font-size: 18px;
}

.btnPrivacy:hover {
  color: var(--highlight-blue-hover);
}

.pPrivacy {
  text-align: justify;
  padding: 10px;
  margin-top: 0;
  margin-bottom: 0;
}

.pPrivacy a {
  color: var(--highlight-blue);
  margin-left: 0.5rem;
}

.pPrivacy a:hover {
  color: var(--highlight-blue-hover);
  transition: all 0.3s;
}

.privacyIcon {
  display: block;
  margin: 0 auto 10px;
}

/* ------------------------------------------------------------------------------------------------ */
/* Media Queries ---------------------------------------------------------- */
/* ------------------------------------------------------------------------------------------------ */

/* Desktop */
@media screen and (min-width: 767px) {
  /* Navbar --------------------------------------------------------------- */
  .title {
    display: inline-block;
  }
  
  .logo {
    margin-left: 10px;
    width: 30px;
  }

  /* Signin/up form ------------------------------------------------------- */
  section {
    background: var(--bg-light-black);
    box-shadow: 1px 2px 10px var(--bg-light-black);
    margin-top: 75px;
    padding: 10px 20px;
    border-radius: 25px;
  }
  /* Profile -------------------------------------------------------------- */
  .img_container img {
    width: 200px;
    height: 200px;
  }
  /* Home ----------------------------------------------------------------- */
  .home_section {
    grid-template-columns: 2fr 3fr;
  }

  .no_convo {
    font-size: 1.5rem;
  }

  img[alt="waving hand"] {
    display: inline;
   }

  /* Users ---------------------------------------------------------------- */
  .user_detail h4 {
    display: initial;
    min-width: 250px;
  }
  
  .avatar {
    width: 60px;
    /* height: 50px; */
    border-radius: 50%;
    margin-left: initial;
    /* border: 1px solid var(--bg-light-black); */
  }
  /* Chat ----------------------------------------------------------------- */
  .message_form {
    left: 30%;
    right: 0;
    bottom: 5px;
  }

  .message_wrapper p {
    max-width: 75%;
  }

  /* Message ---------------------------------------------------------------- */
  .me {
  margin-right: 25px;
  }

  .friend {
  margin-left: 25px;
  }

  .message_wrapper p {
    padding: 40px;
  }

}


